/* stylelint-disable-next-line value-keyword-case */
/** Bulma: Variables **/
/** Main **/
.image-container {
  flex: none;
  position: relative;
  width: 224px;
  height: 224px;
  margin: 0.75rem;
  padding: 11px;
  overflow: hidden;
  align-items: center;
  border: 1px solid hsl(0deg, 0%, 48%);
}
.image-container .title {
  font-weight: normal;
  word-break: break-all;
}
.image-container .image {
  display: flex;
  height: 200px;
  width: 200px;
  align-items: center;
  justify-content: center;
}
.image-container .image img {
  max-height: 100%;
  max-width: 100%;
  height: auto;
  width: auto;
}
.image-container .details {
  position: absolute;
  left: 11px;
  bottom: 11px;
  right: 11px;
  background-color: rgba(0, 0, 0, 0.5607843137);
  color: #eff0f1;
  padding: 3px;
  font-size: 0.75rem;
}
.image-container .details p {
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
}
.image-container .details p.name {
  font-weight: bold;
}